import React, { FC } from 'react';
import s from './AuthButton.module.scss';

interface AuthButtonProps {
  buttonText: string;
  image: React.FunctionComponent<React.SVGAttributes<SVGElement>>;
  onClick: () => void
}

const AuthButton: FC<AuthButtonProps> = (props) => {
  const { buttonText, image: Image, onClick } = props;

  return (
    <button className={s.container} onClick={onClick}>
      <Image />
      <div className={s.text}>
        { buttonText }
      </div>
    </button>
  );
};

export { AuthButton };