import React from 'react';
import { AuthButton } from 'components/AuthButton/AuthButton';
import s from './Auth0Methods.module.scss';
import { useAuth0 } from '@auth0/auth0-react';
import { Button } from 'types/types';


type Auth0MethodsProps = {
  buttons: Button[];
}

const Auth0Methods = (props: Auth0MethodsProps) => {
  const { buttons } = props;
  
  const { loginWithPopup } = useAuth0();

  const loginWithAuth0 = (connection: string) => {
    loginWithPopup({ authorizationParams: { connection } });
  };
  
  const buttonsList = buttons.map(button => {
    return (
      <AuthButton
        buttonText={button.methodName}
        image={ button.image }
        key={ button.methodName }
        onClick={() => loginWithAuth0(button.connection)}
      />
    );
  });
  
  return (
    <div className={s.container}>
      <div className={s.buttons}>
        { buttonsList }
      </div>
      <div className={s.divider}>
        <div className={s.stroke}></div>
        <div className={s.dividerText}>or</div>
        <div className={s.stroke}></div>
      </div>
    </div>
  );
};

export default Auth0Methods;